.Modal {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 1rem;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 100;
  -webkit-animation: fadeInBackground 0.2s;
          animation: fadeInBackground 0.2s;
}

.Modal__container {
  min-width: 40%; 
  max-width: 100%;
  min-height: 3em;
  max-height: 100vh;
  overflow: scroll;
  position: relative;
  background-color: white;
  color: #111111;
  -webkit-animation: fadeInModal 0.2s;
          animation: fadeInModal 0.2s;
}

@media(min-width: 400px) {
  .Modal__container {
    max-width: 80%;
  }
}

.Modal__close {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: 0;
  font-size: 2em;
  font-family: inherit;
  padding: 0 0.5em;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
   background: none; 
}

.Modal__close:focus, .Modal__close:hover {
  color: #444444;
}

.Modal[data-hiding=true] {
  -webkit-animation: fadeOutBackground 0.2s forwards;
          animation: fadeOutBackground 0.2s forwards;
}

.Modal[data-hiding=true] .Modal__container {
  -webkit-animation: fadeOutModal 0.2s forwards;
          animation: fadeOutModal 0.2s forwards;
}

@-webkit-keyframes fadeInBackground {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeInBackground {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadeInModal {
  from {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
    opacity: 0;
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeInModal {
  from {
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
    opacity: 0;
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@-webkit-keyframes fadeOutBackground {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fadeOutBackground {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@-webkit-keyframes fadeOutModal {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }

  to {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
    opacity: 0;
  }
}

@keyframes fadeOutModal {
  from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }

  to {
    -webkit-transform: translateY(-10%);
            transform: translateY(-10%);
    opacity: 0;
  }
}


